/* open-sans-300 - latin */
@font-face {
  font-family: 'Open Sans Light';
  font-style: normal;
  font-weight: 300;
  src: url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-300.eot'); /* IE9 Compat Modes */
  src: local(''),
       // url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-300.eot#iefix') format('embedded-opentype" | absolute_url }}'), /* IE6-IE8 */
       url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
       url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-300.woff') format('woff'), /* Modern Browsers */
       // url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-300.ttf') format('truetype" | absolute_url }}'), /* Safari, Android, iOS */
       // url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-300.svg#OpenSans') format('svg" | absolute_url }}'); /* Legacy iOS */
}
/* open-sans-regular - latin */
@font-face {
  font-family: 'Open Sans Regular';
  font-style: normal;
  font-weight: 400;
  src: url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       // url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-regular.eot#iefix') format('embedded-opentype" | absolute_url }}'), /* IE6-IE8 */
       url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-regular.woff') format('woff'), /* Modern Browsers */
       // url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-regular.ttf') format('truetype" | absolute_url }}'), /* Safari, Android, iOS */
       // url('/project-gray/assets/fonts/OpenSans/open-sans-v18-latin-regular.svg#OpenSans') format('svg" | absolute_url }}'); /* Legacy iOS */
}

/* text */

$body-font:       'Open Sans Regular', 'Verdana', 'sans-serif';
$heading-font:    'Open Sans Light', 'Verdana', 'sans-serif';

/* Colors */

$dark-background:#440027;
$light-text:#D49A6A;
$light-link:#FFD1AA;
$light-link-hover:#CD88AF;
$medium-background:#801515;
$light-background:#F7E4EF;
$dark-text:#444444;
$code:#3C1553;
$muted-text:#777777;

/* Image Heights */

$parallax-height: 350px;

/* Viewer proportions */

$viewer-height: 100%;
$max-width: 100%;


@import
	"facets";
